import commodityItem from "../components/commodity-item/commodity-item.vue"
export default {
	name:'commercial-tenant-type',//分类页面
	components:{
		commodityItem
	},
	props:{
		pageHeight:{
			type:Number,
			default:0
		}
	},
	data() {
		return {
			tabActive:0,//当前选中tab
			commodity:50,//
		};
	},
	mounted() {
		this.$emit('event',{scrolltolower:this.scrolltolower})
	},
	methods:{
		// 左边tab被点击
		setTab(index){
			this.tabActive = index
		},
		//滚动到底部
		scrolltolower(){
			this.tabActive ++
			this.commodity = 0
			uni.showLoading({
				title:"加载更多"
			})
			setTimeout(()=>{
				uni.hideLoading()
				this.commodity = 50
			},1500)
		}
	}
}
